import React from "react";
import { Table } from "mtforms";

import { useParams } from "react-router-dom";
import { useGetUserById } from "../hooks";
import { single_columns } from "../Report/column";
import Layout from "../../../layout";

const User = () => {
  const { id } = useParams();
  const { data } = useGetUserById(id);

  return (
    <Layout name="User" title={`${data?.first_name} Attendance History`}>
      <Table columns={single_columns} data={data?.attendance_history} />
    </Layout>
  );
};

export default User;
